/* Colors */

$transparent: rgba(0,0,0,0) !default;

/* Reset styles */

html {
    -webkit-tap-highlight-color: $transparent;
    color: $color-white;
    font: $font-text;
}

html,
body {
    width: 100%;
    height: 100%;
    outline: 0;
    border: 0;
    background-color: #607d8b;
    -ms-text-size-adjust: auto;
    -webkit-text-size-adjust: auto;
    cursor: crosshair !important;
    overflow-x: hidden;
}


* {
    padding: 0;
    margin: 0;
    &,
    &::before,
    &::after {
        box-sizing: border-box;
    }
    &:focus,
    &:active {
        outline: none;
    }
}

@-ms-viewport {
    width: device-width;
}

@media screen and (max-width: 400px) {
    @-ms-viewport {
        width: 300px;
    }
    @-o-viewport {
        width: device-width;
    }
    @viewport {
        width: device-width;
    }
}

/* Animations */

@keyframes rotate {
    0% { transform: rotate(0deg) }
    50% { transform: rotate(180deg) }
    100% { transform: rotate(360deg) }
}

@keyframes stop {
    0% { transform: rotate(0deg) }
    70% { transform: rotate(252deg) }
    80% { transform: rotate(288deg) }
    100% { transform: rotate(360deg) }
}

@keyframes showDescription {
    0% { bottom: -120px; opacity: 0; }
    1% { bottom: -120px; opacity: 0; }
    100% { bottom: 50px; opacity: 1; }
}

@keyframes hideDescription {
    0% { bottom: 50px; opacity: 1; }
    1% { bottom: 50px; opacity: 1; }
    100% { bottom: -120px; opacity: 0; }
}

/* App */

body {
    background-image: url("data:image/svg+xml;base64,");
    #bg-app {
        width: 100%;
        height: 100%;
        background: radial-gradient(ellipse at center, rgba(255,255,255,0) 0%,rgba(69,103,120,1) 100%);
        #root { 
            .ant-layout,
            .ant-layout-header,
            .ant-layout-footer {
                background: $transparent;
            }
            .mobile-fix,
            svg {
                -webkit-tap-highlight-color: $transparent !important;
                -webkit-focus-ring-color: $transparent !important;
                -moz-user-select: none !important;
                -webkit-user-select: none !important;
                -ms-user-select: none !important;
                user-select: none !important;
                outline: none !important;
            }
            
            .icon-lamp {
                outline: none !important;
            }
            .align-left {
                text-align: left !important;
            }
            .align-right {
                text-align: right !important;
            }
            .center {
                text-align: center !important;
            }
            .mb-0 {
                margin-bottom: 0 !important;
            }
            .mb-10 {
                margin-bottom: 10px !important;
            }
            .mb-20 {
                margin-bottom: 20px !important;
            }
            .mt-20 {
                margin-top: 20px !important;
            }
            .hidden {
                display: none !important;
            }
            .ant-btn-background-ghost {
                background-color: $color-deep-black !important;
                padding: 15px;
                height: auto;
                &.first {
                    margin-right: 10px;
                }
                span {
                    font-size: 2.5rem;
                }
                i {
                    font-size: 40px;
                }
            }
            @media only screen and (max-width: $media-small) {
                .mobile-show {
                    display: block !important;
                    max-height: unset !important;
                }
                .mobile-hide {
                    display: none !important;
                }
                .mobile-center {
                    text-align: center !important;
                }
            }
        }
    }    
}
